import React from "react"
import Seller from "./Seller"
import Star from "../images/star.png"
import Yes from "../images/yes-btn.png"
import No from "../images/no-btn.png"
import Item1Img from "../images/item1.png"
import Item2Img from "../images/item2.png"
import Item3Img from "../images/item3.png"
import Item4Img from "../images/item4.png"
import Item5Img from "../images/item5.png"
import Jenny from "../images/Jenny.png"
import Nancy from "../images/Nancy.png"
import Uriel from "../images/Uriel.png"
import Rachel from "../images/Rachel.png"
import Mia from "../images/Mia.png"


export default function Item(props) {
    
    const styles = {
        visibility: props.show ? "visible" : "hidden"
    }
    
    let itemPic
    let sellerPic

    if (props.id === 1)
        {
            itemPic = Item1Img
            sellerPic = Jenny
        }
        else if (props.id === 2)
        {
            itemPic = Item2Img
            sellerPic = Nancy
        }
        else if (props.id === 3)
        {
            itemPic = Item3Img
            sellerPic = Uriel
        }
        else if (props.id === 4)
        {
            itemPic = Item4Img
            sellerPic = Rachel
        }
        else if (props.id === 5)
        {
            itemPic = Item5Img
            sellerPic = Mia
        }


    return (
        <div 
        style={styles}
        className="item-container">
            <img src={itemPic} className="item-pic"></img>
            <div className="seller-container">
            <div className="seller-card">
           <div className="seller-details">
                <img src={sellerPic} className="seller-image"></img>
                <span className="seller-name">{props.user}</span>
                <span className="seller-location">{props.location}</span>
                <span className="seller-rating">{props.rating} <img src={Star} className="star"></img></span>
           </div>
           <hr className="item-div" />
           <div className="item-details">
                <span className="item-title">{props.title}</span>
                <span className="details-header">Item</span>
                <span className="details-text">{props.item}</span>
                <span className="details-header">Size</span>
                <span className="details-text">{props.size}</span>
                <span className="details-header">Brand</span>
                <span className="details-text">{props.brand}</span>
                <span className="details-header">Description</span>
                <span className="details-text">{props.description}</span>
                </div>
            <div className="buttons-line">
            <img src={No} onClick={props.toggle} className="button1"></img>
            <img src={Yes} onClick={props.toggle} className="button2"></img>
            </div>
           </div>

        
            </div>
        </div>
    )
}