import { render } from "@testing-library/react"
import React, { Component } from "react"
import Alexandra from "../images/Alexandra.png"
import EditProfile from "./EditProfile"
import axios from "axios";
import ArrayAssemble from "./ArrayAssemble";


export default class Profile extends Component {
    
    state ={
        loading: true,
        user: []
    };

    componentDidMount() {
        axios
            .get("http://localhost:8000/api/admin/4/")//interacting with this breaks the cards
            .then((res) => (
                this.setState({
                    loading: false,
                    user: res.data,
                })
                )
            )
            .catch((err) => console.log(err));
            
    }

    

    render() {
    return (
        <div className="profile">
            <img src={Alexandra} className="profile--userimage"></img>
                <div>
                <p className="profile--username">{this.state.user.username}</p>
                <p className="profile--edit">
                {this.state.loading && <p className="loading-text">Profile is loading...</p>}
                {
            <EditProfile 
            key={this.state.user.username} 
            {...this.state.user}
            />
            }
                </p>
                </div> 
        </div>
    );
    }
}