import classnames from 'classnames';
import React, { Component } from 'react';
import ToggleButton from './ToggleButton';

export default function Settings() {
    return(
        <div className="settings-container">
            <li>
                <div className="setting">
                Notifications
                <ToggleButton />
                </div>
            </li>
            <li>
                <div className="setting">
                Hide Profile
                <ToggleButton />
                </div>
            </li>
        </div>
    )
}

