import React from "react"
import Sophie from "../images/Sophie.png"
import Adam from "../images/Adam.png"
import Samantha from "../images/Samantha.png"

export default function Chat(props) {
    
    let pic
    if (props.id === 1)
        pic = Sophie
        else if (props.id === 2)
                pic = Adam
                else pic = Samantha

    return (
            <div className="chat--item">
                <img src={pic} className="chat--userimage"></img>
                <div>
                <p className="chat--username">{props.username}</p>
                <p className="chat--chatpreview">{props.preview}</p>
                </div> 
            </div>
    )
}