import React, {Component} from "react"
import PriceCatergories from "./PriceCategories"
import Item from "./Item"
import items from "../items"
import "../style.css"
import 'bootstrap/dist/css/bootstrap.css';
import axios from "axios";
import { render } from "@testing-library/react";
import ToggleFunction from "./ToggleFunction";
import ArrayAssemble from "./ArrayAssemble";
import { ReactDOM } from "react-dom"


export default class Main extends Component {

    ToggleFunction = (id) => {
        this.setState({
            cards: this.state.cards.map((card) => {return card.id === id ? {...card, show: !card.show} : card}),
        })
        };

    state = {
        cards: [],
        loading: true,
    };
    
    componentDidMount() {
        axios
            .get("http://localhost:8000/api/items/")
            .then((res) => (
                this.setState({
                    loading: false,
                    cards: ArrayAssemble(res.data),
                })
                )
            )
            .catch((err) => console.log("axios error"));

    }

    
  
  
    render() {
    return ( 
        <main className="main">
            <h1 className="main--swapparelHeader">swapparel</h1> 
            <hr className="main--divider" />
            <PriceCatergories />
            {this.state.loading && <p className="loading-text">Items are loading...</p>}
            {this.state.cards.map((card) =>
            <Item 
            key={card.id} 
            toggle={() => this.ToggleFunction(card.id)}
            {...card}
            />
            )}
        </main>
    );
    }
}